import zanCalendar from '@/components/quick-calendar/calendar';
import pickRegions from '@/components/pick-regions/pick-regions.vue'
export default {
	components:{
		zanCalendar,
		pickRegions
	},
	data() {
		return {
			date: '2020-05-22',//日期
			time: '13-26',//时间
			isHourShow: false,//是否显示时辰（小时）
			isMinShow: false,//是否显示分钟
			lunarType: 'number', //年月日显示方式
			dateDialog: false,//是否弹出日历组件
			calendar:'',//选中的日期
			type:0,//默认阳历
			endDate:'',
			wishList:[],
			radio:0,
			step:1,
			form:{
				year:'',
				month:'',
				day:'',
				hour:''
			},
			sex:['男','女'],
			sexIndex:0,
			region:[],//默认地址
			region1:[],//默认地址
			defaultRegion:['广东省','广州市','番禺区'],
			defaultRegionCode:'440113',
			nameForom:{
				surname:'',
				name:''
			}
		}
	},
	computed:{
		regionName(){
			// 转为字符串
			return this.region.map(item=>item.name).join(' ')
		},
		regionName1(){
			// 转为字符串
			return this.region1.map(item=>item.name).join(' ')
		}
	},
	onLoad() {
		uni.getStorage({
		    key: 'wishList',
		    success: (res)=>{
				console.log(res.data)
		        this.wishList=res.data||[]
		    }
		});
	},
	methods: {
		goMatchDojo(){
			uni.redirectTo({
				url:'/pages/matchDojo/matchDojo'
			})
		},
		//选择性别
		selSex(index){
			this.sexIndex=index
			console.log(index)
		},
		//选择日历
		selRadio(index){
			console.log(index)
			if(this.radio===index){
				return
			}
			this.radio=index
		},
		submit(){
			console.log(this.form)
			if(this.step==4){
				return
			}
			this.step++
		},
		//示例，展示三种不同的选择调度
		getType(type){
			if(type==='solar'){
				this.type=0
			}else{
				this.type=1
			}
			console.log(this.type)
		},
		showDateDialog: function(type, lunarType) {
			switch(type){
				case 1:
					this.isHourShow = false;
					this.isMinShow = false;
				break;
				case 2:
					this.isHourShow = true;
					this.isMinShow = false;
				break;
				case 3:
					this.isHourShow = true;
					this.isMinShow = true;
				break;
			}
		
			this.dateDialog = true;
			this.lunarType = lunarType;
		},
		//示例，在为确认是就点击了取消，直接关闭弹窗
		closeDialog: function() {
			this.dateDialog = false;
		},
		//示例，点击了确认后的相关操作，并再次点击确认时间后的返回，这里可以写自己的操作了
		confirmDialog: function(e) {
			console.log(e);
			let str=''
			if(e.type==0){
				str='阳历:'+e.t1
			}else{
				str='阴历:'+e.t2
			}
			this.endDate=str
			console.log("选择的日期是：" + e.date);
			console.log("选择的时间是：" + e.time);
			this.closeDialog()
		},
		// 获取选择的地区
		handleGetRegion(region){
			this.region = region
		},
		handleGetRegion1(region){
			this.region1 = region	
		}
	}
}
